___CSS_LOADER_EXPORT___.push([module.id, "\n.cloud_py_api-configuration[data-v-5029571e] {\n\tmargin: 20px;\n\ttext-align: center;\n}\nh2[data-v-5029571e] {\n\tmargin: 20px 0;\n}\n", "",{"version":3,"sources":["webpack://./src/views/AppConfiguration.vue"],"names":[],"mappings":";AA6EA;CACA,YAAA;CACA,kBAAA;AACA;AAEA;CACA,cAAA;AACA","sourcesContent":["<!--\n - @copyright Copyright (c) 2021-2022 Andrey Borysenko <andrey18106x@gmail.com>\n -\n - @copyright Copyright (c) 2021-2022 Alexander Piskun <bigcat88@icloud.com>\n -\n - @author 2021-2022 Andrey Borysenko <andrey18106x@gmail.com>\n -\n - @license AGPL-3.0-or-later\n -\n - This program is free software: you can redistribute it and/or modify\n - it under the terms of the GNU Affero General Public License as\n - published by the Free Software Foundation, either version 3 of the\n - License, or (at your option) any later version.\n -\n - This program is distributed in the hope that it will be useful,\n - but WITHOUT ANY WARRANTY; without even the implied warranty of\n - MERCHANTABILITY or FITNESS FOR A PARTICULAR PURPOSE. See the\n - GNU Affero General Public License for more details.\n -\n - You should have received a copy of the GNU Affero General Public License\n - along with this program. If not, see <http://www.gnu.org/licenses/>.\n -\n -->\n\n<template>\n\t<div class=\"cloud_py_api-configuration\">\n\t\t<h2>{{ t('cloud_py_api', 'Cloud Py API App Configuration') }}</h2>\n\t\t<p>{{ t('cloud_py_api', 'Here will be configuration steps after installing app, that using this Framework') }}</p>\n\t\t<span v-if=\"loading\" class=\"icon-loading\" />\n\t\t<div v-else-if=\"app\" class=\"app-info\">\n\t\t\t<p>id: {{ app.id }}</p>\n\t\t\t<p>appId: {{ app.app_id }}</p>\n\t\t\t<p>token: {{ app.token }}</p>\n\t\t\t<div class=\"actions\">\n\t\t\t\t<p>Actions for Python requirements (will be listed requirements.txt)</p>\n\t\t\t\t<button>{{ t('cloud_py_api', 'Install') }}</button>\n\t\t\t\t<button>{{ t('cloud_py_api', 'Update') }}</button>\n\t\t\t\t<button>{{ t('cloud_py_api', 'Delete') }}</button>\n\t\t\t</div>\n\t\t</div>\n\t</div>\n</template>\n\n<script>\nimport axios from '@nextcloud/axios'\nimport { generateUrl } from '@nextcloud/router'\n\nexport default {\n\tname: 'AppConfiguration',\n\tdata() {\n\t\treturn {\n\t\t\tloading: false,\n\t\t\tapp: null,\n\t\t}\n\t},\n\tbeforeMount() {\n\t\tthis.getApp()\n\t},\n\tmounted() {\n\t\tthis.$emit('update:loading', false)\n\t},\n\tmethods: {\n\t\tgetApp() {\n\t\t\tthis.loading = true\n\t\t\taxios.get(generateUrl(`/apps/cloud_py_api/api/v1/apps/${this.$route.params.appId}`)).then(res => {\n\t\t\t\tthis.app = res.data\n\t\t\t\tthis.loading = false\n\t\t\t}).catch(err => {\n\t\t\t\tconsole.debug(err)\n\t\t\t\tthis.loading = false\n\t\t\t})\n\t\t},\n\t},\n}\n</script>\n\n<style scoped>\n.cloud_py_api-configuration {\n\tmargin: 20px;\n\ttext-align: center;\n}\n\nh2 {\n\tmargin: 20px 0;\n}\n</style>\n"],"sourceRoot":""}]);
